<template>
    <div class="templates">

        <main-header />

        <section>
            <div class="container">
                <h2>Templates</h2>
                <div class="filter-sorting flex space-between">
                    <p>
                        <strong>Filter By:</strong>
                        <button v-for="category in categories" :key="category.id" @click="selectCategory(category)"
                            :class="{ selected: (category.name === categoryName)}">
                            {{category.name}}
                        </button>
                    </p>
                    <p>
                        <strong>Sort By:</strong>
                        <button :class="{ selected: (sort === '')}" @click="sortBy('')">None</button>
                        <button :class="{ selected: (sort === 'name')}" @click="sortBy('name')">Name</button>
                        <button :class="{ selected: (sort === 'date')}" @click="sortBy('date')">Date</button>
                    </p>
                </div>
                <template v-if="sitesForDisplay">
                    <site-list :sites="sitesForDisplay" :addSite="false"></site-list>
                </template>
            </div>
        </section>

        <main-footer />

    </div>
</template>

<script>
import MainHeader from '@/components/MainHeader.vue';
import SiteList from '@/components/SiteList.vue';
import MainFooter from '@/components/MainFooter.vue';

export default {
    name: 'templates',
    components: { MainHeader , SiteList, MainFooter },
    data() {
        return {
            templates: [
                { id: '5b5773b1e7179a2ae6dc2885', category: ['food', 'business'] },
                { id: '5b65d0b2252bd321c540eef0', category: ['realestate', 'business'] },
                { id: '5b622372bf14b7d0080b7953', category: ['business'] },
                { id: '5b62d1ed5bc9a1b3fc8264e2', category: ['business'] },
                { id: '5b62c26d5bc9a1b3fc8264e0', category: ['food', 'business']},
                { id: '5b65f614252bd321c540eef1', category: ['donations'] },
                { id: '5b66aeff252bd321c540eef2', category: ['business'] },
                { id: '5b62ca1d5bc9a1b3fc8264e1', category: ['business'] },
                { id: '5b57fffce7179a0dd584ecbd', category: ['business'] },
                { id: '5b6964ce81fdce79056a1c8e', category: ['cv'] },
                { id: '5b6973c181fdce79056a1c8f', category: ['business'] },
            ],
            categories: [
                { id: '', name: 'All' },
                { id: 'food', name: 'Food' },
                { id: 'business', name: 'Business' },
                { id: 'realestate', name: 'Real Estate' },
                { id: 'donations', name: 'Donations' },
                { id: 'cv', name: 'CV' },
            ],
            selectedCategory: { id: '' },
            sort: '',
            sites: [],
        }
    },
    created() {
        this.loadTemplates();
    },
    methods: {
        selectCategory(category) {
            this.selectedCategory = category;
        },
        sortBy(sort) {
            this.sort = sort;
        },
        loadTemplates() {
            let siteIds = this.templates.map( item => item.id );
            this.$store.dispatch({ type: 'loadSitesByIds', siteIds })
                .then(sites => this.sites = sites);
        }
    },
    computed: {
        categoryName() {
            let cat = this.categories.find(category => category.id === this.selectedCategory.id );
            return cat.name;
        },
        sitesForDisplay() {

            // Filter
            let sites = [];
            if (!this.selectedCategory.id) {
                sites = this.sites;
            } else {
                let siteIdsInSelectedCategory = this.templates.filter(item => {
                    return item.category.includes(this.selectedCategory.id);
                });
                sites = this.sites.filter(site => {
                    return siteIdsInSelectedCategory.find(selected => selected.id === site._id)
                });
            }

            // Sort
            switch (this.sort) {
                case 'name':
                    sites.sort((a,b) => {
                        if (a.name.toUpperCase() < b.name.toUpperCase()) return -1;
                        if (a.name.toUpperCase() > b.name.toUpperCase()) return 1;
                        return 0;
                    });
                    break;
                case 'date':
                    sites.sort((a,b) => {
                        return a.date.updated - b.date.updated;
                    });
                    break;
            }

            // Sort ASC/DESC
            // if (this.filter.sortASC) booksToShow.reverse();

            // Return sites
            return sites;
        }
    }
};
</script>

<style scoped lang="scss">
section {
    padding: 20px 0;

    h2 {
        text-align: center;
    }

    p {
        margin-top: 0;
    }

    .filter-sorting {
        padding: 0 15px;
    }

    button {
        margin-left: 10px;
        padding: 5px 10px;
        border: 0;
        border-radius: 5px;
        background-color: #eee;
        font-size: 12px;
        transition: 0.3s;

        &:hover,
        &.selected {
            background-color: #ccc;
        }
    }
}
@media (max-width:720px) {
    .filter-sorting {
        flex-direction: column;

        button {
            margin-bottom: 15px;
        }
    }
}
</style>
