<template>
    <section class="editor-footer flex space-around align-center flex-wrap">

        <div class="flex-grow" title="Details" @click="setSiteDetailsScreen">
            <font-awesome-icon :icon="['fas', 'cog']" />
        </div>
        <div class="flex-grow" title="Save" @click="publish">
            <font-awesome-icon :icon="['fas', 'save']" />
        </div>
        <div class="flex-grow" title="Preview" @click="showDisplay" target="_blank">
            <font-awesome-icon :icon="['fas', 'eye']" />
        </div>
        <div class="flex-grow" title="Exit" @click="exit">
            <font-awesome-icon :icon="['fas', 'sign-out-alt']" />
        </div>

    </section>
</template>

<script>
export default {
    name: 'editor-footer',
    props: [ 'site' ],
    data() {
        return {
            newUser: {
                userName: '',
                password: ''
            }
        }
    },
    computed:{
        loggedInUser() {
            return this.$store.getters.loggedInUser;
        },
    },
    methods: {
        setLogginUser() {
            var user = localStorage.getItem('loggedInUser');
            user = JSON.parse(user);
            if (user) {
                this.$store.commit({ type: 'setUser', user });
                this.$store.commit({ type: 'updateUserId', user });
            }
        },
        showDisplay() {
            if (!this.site._id) return;
            let route = this.$router.resolve({path: '/' + this.site._id});
            window.open(route.href, '_blank');
        },
        setSiteDetailsScreen() {
           this.$store.commit({ type: 'setEditorScreen', screen: 'editor-site-details' });
        },
        login() {
            let user = { userName: this.newUser.userName, password: this.newUser.password };
            this.$store.dispatch({ type: 'login', user })
                .then(user => {
                    if (user) {
                        this.$swal({
                            title: 'Login',
                            text: 'Logged in successfully.',
                            confirmButtonText: 'Ok',
                            confirmButtonColor: '#333',
                            timer: 5000
                        })
                        .then(() => {
                            this.$store.commit({ type: 'updateUserId', user });
                            this.$store.dispatch({ type: 'createNewSite' })
                                .then(site => {
                                    if (site) {
                                        this.$store.commit({ type: 'loadSite', site });
                                        this.$store.commit({ type: 'addSite', site });
                                        this.$swal({
                                            title: 'Site Created',
                                            text: 'The site has been sucsecfully created.',
                                            confirmButtonText: 'Ok',
                                            confirmButtonColor: '#333',
                                            timer: 5000
                                        })
                                        .then(() => this.$router.push(`/${site._id}/edit`));
                                    } else {
                                        this.$swal({
                                            title: 'Site Not Created',
                                            text: 'An error accord. The site was not created. If you havent logged in yet please do and try again.',
                                            confirmButtonText: 'Ok',
                                            confirmButtonColor: '#333',
                                            timer: 5000
                                        });
                                    }
                                });
                                
                            })
                    } else {
                        this.$swal({
                            title: 'Login',
                            text: 'Wrong credentials. Please try again.',
                            confirmButtonText: 'Ok',
                            confirmButtonColor: '#333',
                            timer: 5000
                        });
                    }
                });
        },
        publish() {
            this.setSiteDetailsScreen();
            if (this.site._id) {
                // Update existing site
                this.$store.dispatch({ type: 'publishSite' })
                    .then(isUpdated => {
                        if (isUpdated) {
                            this.$swal({
                                title: 'Site Updated',
                                text: 'The site has been sucsecfully updated.',
                                confirmButtonText: 'Ok',
                                confirmButtonColor: '#333',
                                timer: 5000
                            });
                        } else {
                            this.$swal({
                                title: 'Site Not Updated',
                                text: 'An error accord. The site was not updated. Please try again later.',
                                confirmButtonText: 'Ok',
                                confirmButtonColor: '#333',
                                timer: 5000
                            });
                        }
                    });
            } else {
                // Create new site
                if (!this.loggedInUser._id) {
                    // Ask user to login
                    this.$swal({
                        title: 'Create Site',
                        text: 'Please login to create a new site.',
                        showCancelButton: true,
                        confirmButtonText: 'Signup',
                        confirmButtonColor: '#333',
                        cancelButtonText: 'Login',
                        cancelButtonColor: '#333',
                        html: `
                            <p> 
                               Please Signup or Login to publish this site.
                            </p>`,
                    })
                        .then(res => {
                            // Check if user wants to login or signup
                            console.log(res)
                            if (res.dismiss === 'cancel') {
                                this.$swal({
                                    // User login
                                    title: 'Login',
                                    confirmButtonText: 'Login',
                                    confirmButtonColor: '#333',
                                    html: `
                                        <p>
                                            <label>Username:</label>
                                            <input class="username" type="text" required /> 
                                        </p>
                                        <p>
                                            <label>Password:</label>
                                            <input class="userpass" type="password" required />
                                        </p>
                                    `,
                                    preConfirm: () => {
                                        return new Promise(resolve => {
                                            resolve([
                                                document.querySelector('.username').value,
                                                document.querySelector('.userpass').value,
                                            ]);
                                        })
                                    }
                                })
                                .then(result => {
                                    if (result.value) {
                                        this.newUser.userName = result.value[0];
                                        this.newUser.password = result.value[1];
                                        this.login();
                                    }
                                });

                            } else if (res.value) {
                                // User signup
                                window.open(`${window.location.origin}/#/signup`)
                                this.setLogginUser();
                                if (this.loggedInUser._id) {
                                    this.$store.dispatch({ type: 'createNewSite' })
                                        .then(site => {
                                            if (site) {
                                                this.$store.commit({ type: 'loadSite', site });
                                                this.$store.commit({ type: 'addSite', site });
                                                this.$swal({
                                                    title: 'Site Created',
                                                    text: 'The site has been sucsecfully created.',
                                                    confirmButtonText: 'Ok',
                                                    confirmButtonColor: '#333',
                                                    timer: 5000
                                                })
                                                .then(() => this.$router.push(`/${site._id}/edit`));
                                            } else {
                                                this.$swal({
                                                    title: 'Site Not Created',
                                                    text: 'An error accord. The site was not created. If you havent logged in yet please do and try again.',
                                                    confirmButtonText: 'Ok',
                                                    confirmButtonColor: '#333',
                                                    timer: 5000
                                                });
                                            }
                                        });
                                }
                            }
                        })
                } else {
                    // Create new site for loggedin user
                    this.$store.dispatch({ type: 'createNewSite' })
                        .then(site => {
                            if (site) {
                                this.$store.commit({ type: 'loadSite', site });
                                this.$store.commit({ type: 'addSite', site });
                                this.$swal({
                                    title: 'Site Created',
                                    text: 'The site has been sucsecfully created.',
                                    confirmButtonText: 'Ok',
                                    confirmButtonColor: '#333',
                                    timer: 5000
                                })
                                .then(() => this.$router.push(`/${site._id}/edit`));
                            } else {
                                this.$swal({
                                    title: 'Site Not Created',
                                    text: 'An error accord. The site was not created. Please try again later.',
                                    confirmButtonText: 'Ok',
                                    confirmButtonColor: '#333',
                                    timer: 5000
                                });
                            }
                        });
                }
            }
        },
        exit() {
            this.$router.push('/');
        }
    }
};
</script>

<style scoped lang="scss">
.editor-footer {
    background-color: #333;
    color: #fff;
    text-align: center;

    > div {
        padding: 10px 0;
        font-size: 0.85em;
        line-height: 1.5;
        font-size: 15px;
        cursor: pointer;
        transition: .5s;

        &:hover {
            background-color: #666;
            color: #fff;
            transition: .5s;
        }
    }
}
</style>
